body:has(.switch__input:checked) {
  background-color: var(--fg);
  color: var(--bg);
}

.switch {
  cursor: pointer;
  margin: auto;
  position: relative;
}

.switch__icon, .switch__input {
  display: block;
}

.switch__icon {
  width: .75em;
  height: .75em;
  transition: opacity calc(var(--trans-dur) / 2), transform calc(var(--trans-dur) / 2);
  position: absolute;
  top: .375em;
  right: .375em;
}

.switch__icon polyline {
  transition: stroke-dashoffset calc(var(--trans-dur) / 2);
}

.switch__icon--light, .switch__icon--light polyline {
  transition-delay: calc(var(--trans-dur) / 2);
  transition-timing-function: var(--trans-timing-out);
}

.switch__icon--dark {
  opacity: 0;
  transition-timing-function: var(--trans-timing-in);
  transform: translateX(-.75em)rotate(30deg)scale(.75);
}

.switch__input {
  box-shadow: 0 0 0 .125em hsla(var(--hue), 90%, 50%, 0), .125em .125em .25em hsla(var(--hue), 90%, 10%, .2);
  width: 3em;
  height: 1.5em;
  -ms-appearance: none;
  appearance: none;
  -webkit-tap-highlight-color: transparent;
  transition: background-color var(--trans-dur) var(--trans-timing), box-shadow .15s linear;
  background-color: #6eb3f7;
  border-radius: .75em;
  outline: rgba(0, 0, 0, 0);
  position: relative;
}

.switch__input:focus-visible {
  box-shadow: 0 0 0 .125em hsl(var(--hue), 90%, 50%), .125em .125em .25em hsla(var(--hue), 90%, 10%, .2);
}

.switch__input:before, .switch__input:after {
  content: "";
  display: block;
  position: absolute;
}

.switch__input:before {
  border-radius: inherit;
  transition: background-color var(--trans-dur) var(--trans-timing);
  background-color: #f2cc0d;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  mask-image: linear-gradient(120deg, #000 20%, rgba(0, 0, 0, 0) 80%);
}

.switch__input:after {
  box-shadow: .05em .05em .05em hsla(var(--hue), 90%, 10%, .1);
  width: 1.25em;
  height: 1.25em;
  transition: background-color var(--trans-dur) var(--trans-timing), transform var(--trans-dur) var(--trans-timing);
  z-index: 1;
  background-color: #fff;
  border-radius: 50%;
  top: .125em;
  left: .125em;
}

.switch__input:checked {
  background-color: #a30ac2;
}

.switch__input:checked:before {
  background-color: #0a47c2;
}

.switch__input:checked:after {
  background-color: #000;
  transform: translateX(1.5em);
}

.switch__input:checked ~ .switch__icon--light, .switch__input:checked ~ .switch__icon--light polyline {
  transition-delay: 0s;
  transition-timing-function: var(--trans-timing-in);
}

.switch__input:checked ~ .switch__icon--light {
  opacity: 0;
  transform: translateX(-.75em)rotate(-30deg)scale(.75);
}

.switch__input:checked ~ .switch__icon--light polyline {
  stroke-dashoffset: 1.5px;
}

.switch__input:checked ~ .switch__icon--dark {
  opacity: 1;
  transition-delay: calc(var(--trans-dur) / 2);
  transition-timing-function: var(--trans-timing-out);
  transform: translateX(-1.5em);
}

.switch__sr {
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

